<template>
<div class="scrollBox">
  <header-nav v-if="$utils.showNavBar()"></header-nav>
  <div id='vip_decode' :class="{hasHeader: $utils.showNavBar()}">
    <div class="banner iconSize">
      <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/movie_banner.png" alt="banner">
    </div>
    <div class="tab-list flex-row">
      <div class="tab-item" v-for="(item, index) in list" @click="setActive(index)" :class="{active: activeIndex === index, has_dot: index === 1 && item.has_dot}" :key="index"><span>{{item.name}}</span></div>
    </div>
    <transition name='RightList'>
      <div v-if="activeIndex === 0">
        <div class="input-box">
          <div class="input">
            <input type="text" maxlength="200" v-model="playUrl" placeholder="请粘贴腾讯视频、爱奇艺、优酷等视频地址">
          </div>
          <div class="button" @click="setPlay()">开始播放</div>
        </div>
        <div class="playBox">
          <div class="play-content isWx" v-if="$utils.isWx() && isPlay">
            <play-source></play-source>
          </div>
          <div class="play-content">
            <div class="play-resource-list" v-if="!$utils.isWx() && isPlay">
              <div class="play-resource-item" :class="{active: index === activeResource}" v-for="(item, index) in playListUrl" @click="setActiveResource(index)" :key="index">{{index}}</div>
            </div>
            <div class="iframeBox" v-if="type==='iframe'">
              <div class="video" ref="videoEl"></div>
              <div class="tip">无法播放？<span class="redColor" @click="$router.push('/AwardFeedback?from=movie')">点击报错</span></div>
            </div>
            <div class="scrollTouch imgCountBox" v-infinite-scroll="loadMore">
              <div class="title">支持免费播放以下视频（无广告）</div>
              <div class="imgCount">
                <div class="iconSize" v-for="(item, index) in imgCount" infinite-scroll-distance="200" :key="index">
                  <img :src="`https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/movie/image_0${index < 10 ? ('0' + index) : index}.png`">
                </div>
              </div>
              <div class="loading flex-row" v-if="loading === 1">
                <mt-spinner type="fading-circle"></mt-spinner>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name='leftList'>
      <div v-if="activeIndex === 1" class="playBook">
        <div class="title">1、打开想看的影视平台vip视频，复制浏览器地址</div>
        <div class="image">
          <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/movie/guidld_01.png" alt="引导页">
        </div>
        <div class="title">2、回到当前页面粘贴并点击播放，即可免费播放vip影视</div>
        <div class="image">
          <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/movie_book.png" alt="引导页">
        </div>
      </div>
    </transition>
  </div>
</div>
  
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import PlaySource from '@/components/movie/common/play-source'
export default {
  components: {
    HeaderNav,
    PlaySource
  },
  data () {
    return {
      list: [{
        name: 'VIP视频解析'
      },
      {
        name: '播放教程'
      }],
      activeUrl: '',
      isPlay: false,
      type: '',
      activeIndex: 0,
      loading: 0,
      playUrl: '',
      imgCount: 12,
      playSource: [''],
      activeResource: '',
      playListUrl: {}
    }
  },
  methods: {
    setActive (index) {
      if (index === 1) {
        this.$set(this.list[1], 'has_dot', false)
        this.$utils.cache('playCache', JSON.stringify({time: new Date(this.$utils.timestampToStr(new Date().getTime() + 24 * 3600 * 1000, 'Y/M/D')).getTime()}))
      }
      this.activeIndex = index
    },
    setActiveResource (index) {
      this.activeResource = index
      this.type = ''
      this.setPlay()
    },
    setPlay () {
      if (this.$utils.isWx()) {
        this.isPlay = true
      } else {
        if (!this.playUrl) {
          this.$toast('请输入视频源地址')
        } else if (this.playUrl.indexOf('http') === -1) {
          this.$toast('请输入正确的播放地址')
        } else {
          this.type = ''
          this.isPlay = true
          this.type = 'iframe'
          this.$nextTick(() => {
            this.activeUrl = this.playListUrl[this.activeResource] + this.playUrl
            this.$utils.emptyEl(this.$refs.videoEl)
            let iframe = document.createElement('iframe')
            iframe.setAttribute('allowfullscreen', true)
            iframe.setAttribute('allowtransparency', true)
            iframe.setAttribute('frameborder', 0)
            iframe.className = 'video'
            iframe.setAttribute('src', this.activeUrl)
            this.$refs.videoEl.appendChild(iframe)
          })
        }
        let el = document.getElementsByTagName('video')
        if (el) {
          this.$utils.setfullScreen(el[0])
        } else {
          this.$utils.setfullScreen(document.body)
        }
      }
    },
    loadMore () {
      if (this.loading > 0) {
        return
      }
      this.loading = 1
      setTimeout(() => {
        if (this.imgCount + 12 < 61) {
          this.imgCount += 12
          this.loading = 0
        } else {
          this.imgCount = 61
          this.loading = 2
        }
      }, 1500)
    },
    getVipDecode () {
      this.$api.get('VIP_DECODE', {}, res => {
        let tempArr = Object.keys(res.data)[0]
        this.activeResource = tempArr
        this.playListUrl = res.data
      })
    }
  },
  beforeRouteLeave (to, from, next) {
    console.log(this.$refs.videoEl, this.activeIndex)
    if (this.activeIndex === 1) {
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.activeIndex = 0
      }, 400)
      next(false)
    } else if (this.$refs.videoEl) {
      this.$utils.emptyEl(this.$refs.videoEl)
      next(false)
    } else {
      next()
    }
  },
  mounted () {
    this.getVipDecode()
    if ((!this.$utils.cache('playCache')) || new Date().getTime() > JSON.parse(this.$utils.cache('playCache')).time) {
      this.$set(this.list[1], 'has_dot', true)
    }
  }
}
</script>
<style lang="scss" scoped>
#vip_decode {
  height: 100vh;
  padding-bottom: 80px;
  overflow: auto;
  .banner {
    height: 300px;
  }
  // tab切换区域
  .tab-list {
    border-bottom: 1px solid #e5e5e5;
    .tab-item {
      padding: 40px 0 16px;
      line-height: 45px;
      flex: 1;
      position: relative;
      &.active {
        color: #F4A82B;
        &::after{
          content: "";
          position: absolute;
          left: 50%;
          margin-left: -50px;
          bottom: 0;
          height: 4px;
          width: 100px;
          box-sizing: border-box;
          display: block;
          background: #F4A82B;
        }
      }
      &.has_dot span {
        position: relative;
        &::after {
          content: '';
          position: absolute;
          right: -20px;
          top: 0;
          display: block;
          border-radius: 50%;
          overflow: hidden;
          background: #FF4112;
          width: 12px;
          height: 12px;
        }
      }
    }
  }
  // 输入区域
  .input-box {
    background: #FFFFFF;
    box-shadow: -4px -4px 14px 0 #EBEAEF, 4px 4px 14px 0 #EBEAEF;
    border-radius: 20px;
    margin: 50px 26px;
    height: 350px;
    overflow: hidden;
    .input {
      background: #F5F5F5;
      box-shadow: 0 2px 11px 0 rgba(245,166,35,0.31);
      width: 610px;
      height: 90px;
      margin: 60px auto 55px;
      border-radius: 34px;
      padding-top: 30px;
      box-sizing: border-box;
      input {
        background: none;
        width: 100%;
        font-size: 28px;
        line-height: 34px;
        padding: 0 20px;
        box-sizing: border-box;
      }
    }
    .button {
      width: 446px;
      height: 82px;
      background: #F4A82B;
      border-radius: 34px;
      margin: 0 auto;
      box-sizing: border-box;
      padding-top: 21px;
      line-height: 40px;
      font-size: 28px;
      font-weight: 900;
      text-align: center;
      color: #fff;
    }
  }
  // 播放区域
  .playBox {
    margin-bottom: 50px;
  }
  // 视频图标区域
  .imgCountBox {
    padding-bottom: 100px;
    .title {
      font-size: 33px;
      color: #333333;
      line-height: 45px;
      padding: 0px 30px 30px;
    }
    .imgCount {
      padding: 0 30px;
      .iconSize {
        width: 184px;
        height: 77px;
        overflow: hidden;
        display: inline-block;
        margin-bottom: 35px;
        &:nth-child(3n + 2) {
          margin-left: 57px;
          margin-right: 57px;
        }
      }
    }
  }
  // 播放教程
  .playBook {
    overflow: hidden;
    padding-bottom: 100px;
    margin-bottom: 60px;
    .title {
      font-size: 32px;
      line-height: 40px;
      padding:30px;
    }
    .image {
      padding: 0 30px;
      img {
        width: 100%;
        display: block;
        margin: 0 auto;
      }
    }
  }
  // 播放源
  .play-resource-list {
    white-space: nowrap;
    overflow: auto;
    padding: 0 30px;
    .play-resource-item {
      display: inline-block;
      padding: 40px 0 16px;
      line-height: 45px;
      position: relative;
      margin-right: 40px;
      &.active {
        color: #F4A82B;
        &::after{
          content: "";
          position: absolute;
          left: 50%;
          margin-left: -50px;
          bottom: 0;
          height: 4px;
          width: 100px;
          box-sizing: border-box;
          display: block;
          background: #F4A82B;
        }
      }
    }
  }
  // 播放区域
  .iframeBox {
    .video {
      width: 100%;
      display: block;
      height: 384px;
      margin: 20px 0;
      overflow: hidden;
    }
    .tip {
      text-align: right;
      padding-right: 30px;
      font-size: 28px;
      line-height: 40px;
      padding-bottom: 60px;
      color: #999;
    }
  }
}
</style>



