<template>
  <div id='play-source'>
    <img class="play-image" src="../../../assets/img/movie/video.png" alt="播放框">
    <div class="loading flex-row">
      <mt-spinner type="fading-circle"></mt-spinner>
    </div>
    <div class="mask">
      <div class="play-text">因{{$utils.isWx() ? '微信' : 'QQ'}}屏蔽播放线路，请在掘金宝APP（或手机浏览器）中观看</div>
      <div class="buttonGroup">
        <div class="button" @click="toBroswer">浏览器观看</div>
        <div class="button active" @click="toApp">APP观看(推荐)</div>
      </div>
    </div>
    <div class="normalMask" v-if="showMask" @click="closeMask">
      <div class="title posC">1. 点击右上角</div>
      <div class="title second posC">
        <div class="text">2.在浏览器中点击</div>
        <div class="smallText">(在浏览器中打开)</div>
      </div>
      <div class="line1 line"></div>
      <div class="line2 line"></div>
      <div class="line3 line">
        <div class="text">在浏览器打开</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      showMask: false
    }
  },
  methods: {
    toApp () {
      location.href = 'https://a.app.qq.com/o/simple.jsp?pkgname=com.juejinchain.android'
    },
    toBroswer () {
      this.showMask = true
    },
    closeMask () {
      this.showMask = false
    }
  }
}
</script>
<style lang="scss" scoped>
#play-source {
  position: relative;
  padding-top: 20px;
  height: 384px;
  background: #000;
  overflow: hidden;
  margin: 0;
  .play-image {
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    display: block;
  }
  .mask {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    background: rgba($color: #000000, $alpha: .65)
  }

  .play-text {
    color: #fff;
    font-size: 30px;
    color: #FFFFFF;
    text-align: center;
    margin: 50px auto 0;
    line-height: 42px;
    width: 450px;
  }
  .loading {
    padding: 5px 0;
    margin-bottom: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    top: 150px;
  }
  .buttonGroup {
    display: flex;
    justify-content: center;
    padding: 0 10px;
    margin-top: 90px;
    .button {
      box-sizing: border-box;
      padding-top: 14px;
      height: 64px;
      border: 1px solid #ff7f18;
      color: #fff;
      line-height: 40px;
      font-size: 28px;
      border-radius: 4px;
      width: 188px;
      text-align: center;
      &.active {
        margin-left: 46px;
        background: #ff7f18;
        width: 240px;
      }
    }
  }
  // 浮层样式
  /* 遮罩层 */
  .normalMask {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .5);
    color: #fff;
    overflow: hidden;
    div {
      line-height: 1.5
    }
  }
  .posC {
    position: absolute;
    left: 50%;
    transform: translateX(-50%)
  }
  .normalMask .title {
    top: 175px;
    text-align: center;
  }
  .normalMask .second {
    top: 409px;
  }
  .normalMask .second .smallText {
    font-size: 24px;
    line-height: 34px;
  }
  .normalMask .line {
    position: absolute;
  }
  .normalMask .line1 {
    right: 53px;
    top: 25px;
    width: 89px;
    height: 74px;
    background: url('../../../assets/img/movie/line1.png') no-repeat left top;
    background-size: 100% 100%;
  }
  .normalMask .line2 {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: 1px solid #fff;
    right: 74px;
    top: 154px;
    background-image: url('../../../assets/img/movie/dot.png'),url('../../../assets/img/movie/dot.png'),url('../../../assets/img/movie/dot.png');
    background-size: 14px 14px;
    background-position: 23px center, 53px center, 88px center;
    background-repeat:  no-repeat;
  }
  .normalMask .line3 {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: 1px solid #fff;
    right: 74px;
    top: 379px;
    background: #fff;
    background-image: url('../../../assets/img/movie/oepn_in_browser.png');
    background-size: 40px 40px;
    background-position: 37px 23px;
    background-repeat: no-repeat;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding-bottom: 27px;
    box-sizing: border-box;
  }
  .normalMask .line3 .text {
    font-size: 14px;
    color: #333;
    text-align: center;
  }
}
</style>



